import request from '@/utils/request';
var api = {
  getDictionaryList: '/api/dms/Dictionary/Get',
  deleteDictionary: '/api/dms/Dictionary/Delete',
  getUpdictionaryById: '/api/dms/Dictionary/GetUpById',
  getDowndictionaryById: '/api/dms/Dictionary/GetDown',
  updateDownDictionary: '/api/dms/Dictionary/UpdateDown',
  createDownDictionary: '/api/dms/Dictionary/CreateDown',
  updateUpDictionary: '/api/dms/Dictionary/UpdateUp',
  createUpDictionary: '/api/dms/Dictionary/CreateUp'
}; // 获取数据字典列表

export function getDictionaryList(obj) {
  return request({
    url: api.getDictionaryList,
    method: 'get',
    params: obj,
    headers: {
      'Content-Type': 'application/json'
    }
  });
} // 删除字典

export function deleteDictionary(id) {
  return request({
    url: api.deleteDictionary,
    method: 'post',
    data: {
      id: id
    },
    headers: {
      'Content-Type': 'application/json'
    }
  });
}
